
.v-input-wrapper {
  position: relative;
  display: block;
  height: 2.6rem;
}
.v-input-wrapper .iconfont {
  color: #999;
}
.v-input-wrapper > .iconfont:first-child {
  line-height: 100%;
  position: absolute;
  left: 5px;
  top: 50%;
  margin: -8px 0;
}
.v-input-wrapper > .iconfont:first-child + input {
  padding-left: 30px;
}
.v-input-wrapper input {
  border: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  outline: none;
  text-align: left;
  font-size: inherit;
  padding-left: 0.8rem;
  vertical-align: middle;
}
.v-input-wrapper input[disabled] {
  opacity: 0.6;
  pointer-events: none;
}
.v-input-wrapper:before {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(to bottom, transparent 50%, #d7dde4 50%, #d7dde4 100%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
  z-index: 1;
}
.v-input-wrapper:after {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(to bottom, transparent 50%, #3399ff 50%, #3399ff 100%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
  width: 0;
  transition: width 0.4s ease-in-out;
  z-index: 2;
}
.v-input-focus:after {
  width: 100%;
}
.v-input-clear-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  outline: none;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  width: 40px;
  height: 100%;
  display: none;
}
.v-input-clear .v-input-clear-button {
  display: block;
}
.v-input-clear input {
  padding-right: 40px;
}
.input-clear-fade-enter-active,
.input-clear-fade-leave-active {
  transition: opacity 0.2s;
}
.input-clear-fade-enter,
.confirm-fade-leave-active {
  opacity: 0;
}



