
.v-btn {
  border: 0;
  outline: none;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 2.6rem;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 6px;
  font-size: inherit;
  cursor: default;
}
.v-btn:before {
  border: 1px solid #d7dde4;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  position: absolute;
  left: -50%;
  top: -50%;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  content: '';
  border-radius: 12px;
}
.v-btn:active {
  box-shadow: 1px 0 100px rgba(0, 0, 0, 0.15) inset;
}
.v-btn-primary {
  background-color: #3399ff;
  color: #fff;
}
.v-btn-primary:before {
  border-color: #3399ff;
  display: none;
}
.v-btn-warning {
  background-color: #ff9900;
  color: #fff;
}
.v-btn-warning:before {
  border-color: #ff9900;
  display: none;
}
.v-btn-dashed {
  background-color: #fff;
  color: #3399ff;
  border: 0;
}
.v-btn-dashed:active {
  box-shadow: none;
}
.v-btn-dashed:before {
  border-color: #3399ff;
}
.v-btn[disabled] {
  opacity: 0.6;
  pointer-events: none;
}



