
.v-header {
  background: #fff;
}
.v-header-nav {
  color: #fff;
  background-color: #3399ff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 2.7rem;
}
.v-header .btn-pull {
  color: inherit;
  height: inherit;
  min-width: 40px;
  border: 0;
  outline: none;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}
.v-header .btn-pull:active {
  background-color: rgba(0, 0, 0, 0.1);
}
.v-header-back {
  margin-right: -10px;
  position: relative;
  z-index: 1;
}
.v-header-back:active {
  background: transparent!important;
}
.v-header-title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 1.1rem;
  overflow: hidden;
}



