
.v-password {
  position: relative;
}
.v-password label {
  position: static;
}
.v-password-switch {
  position: absolute;
  right: 0;
  top: 0;
  border: 0;
  outline: none;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  width: 40px;
  height: 100%;
}
.v-password-switch .iconfont {
  font-size: 20px;
  color: #666;
}



