
.v-textarea-wrapper {
  position: relative;
  display: block;
  height: 2.6rem;
  min-height: 2.6rem;
  text-align: left;
}
.v-textarea-wrapper textarea,
.v-textarea-wrapper .v-textarea-shadow {
  padding: 0.8rem;
  width: 100%;
  text-align: inherit;
  font-style: inherit;
  line-height: inherit;
  font-family: inherit;
  box-sizing: border-box;
  min-height: inherit;
  color: inherit;
}
.v-textarea-wrapper textarea {
  height: 100%;
  resize: none;
  border: 0;
  outline: none;
  overflow: hidden;
  vertical-align: middle;
}
.v-textarea-wrapper textarea[disabled] {
  opacity: 0.6;
  pointer-events: none;
}
.v-textarea-wrapper:before {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(to bottom, transparent 50%, #d7dde4 50%, #d7dde4 100%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
  z-index: 1;
}
.v-textarea-wrapper:after {
  content: '';
  display: block;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(to bottom, transparent 50%, #3399ff 50%, #3399ff 100%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
  width: 0;
  transition: width 0.4s ease-in-out;
  z-index: 2;
}
.v-textarea-focus:after {
  width: 100%;
}
.v-textarea-shadow {
  position: absolute;
  left: 0;
  top: 0;
  min-height: 2.6rem;
  word-break: break-all;
  opacity: 0;
  z-index: -1;
}



